body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(240, 238, 234);
  
}
.head{
  display: flex;
  width: 98vw;
  height: 400px;
  /* flex-wrap: wrap; */
  justify-content: space-between;
 
}
.box{
  border: 1px solid black;
  width: 50%;
  height: 100%;
  border-radius: 15px;
  margin: 10px;
  margin-top: 20vh;
}
.box button{
  
  border: 2px solid white;
  color: white;
  padding: 10px 30px 10px;
  font-size: 20px;
  border-radius: 5px;
  margin-top: 25px;
  margin-left: 100px;
  font-weight: bold;
  transition: 1s;

 
}
#hook{
  background-color: rgb(144, 68, 215);
  
}
#hook button{
  background-color: blueviolet;
}
#hooks{
  background-color: rgb(250, 119, 235);
}
#hooks button{
  background-color: rgb(226, 43, 159);
}
button:hover{
  box-shadow: 5px 5px 25px 5px white;
  transform: scale(1.1);
}
#hook h1{
  color: whitesmoke;
  text-shadow: 6px 6px 5px purple;
  margin-left: 25PX;
  margin-top: 100PX;
  font-size: 38PX;
}
h1{
  margin-left: 40PX;
  margin-top: 100PX;
  font-size: 40PX;
  font-weight: bold;
}
#hooks h1{
  color: whitesmoke;
  text-shadow: 6px 6px 5px rgb(254, 59, 189);
  
}
#hook h2{
  color: whitesmoke;
  text-shadow: 3px 3px 5px gray;
  margin-left: 150PX;
  margin-top: 10PX;
  font-size: 38PX;
  border: 2px solid white;
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 5px;
  background-color: blueviolet;
  padding: 10px;
}
#hooks h2{
  color: whitesmoke;
  text-shadow: 3px 3px 5px grey;
  margin-left: 80PX;
  margin-top: 10PX;
  font-size: 38PX;
}
#clock{
  background-color: rgb(136, 216, 136);
}
#clock h1{
  color: whitesmoke;
  text-shadow: 5px 5px 6px rgb(4, 87, 4);
}
#clock h2{
  color: whitesmoke;
  text-shadow: 3px 3px 6px black;
  margin-left: 30PX;
  margin-top: 10PX;
  font-size: 38PX;
  border: 2px solid white;
  background-color: rgb(79, 172, 79);
  border-radius: 10px;
  text-align: center;
  width: 280px;
  height: 60px;
}

#identity{
  background-color: rgb(252, 195, 89);
}
#identity button{
  background-color: orange;
  margin-top:10px;
}
#identity h1{
  color: whitesmoke;
  text-shadow: 5px 5px 7px black;
  
}
input{
  background-color:whitesmoke;
  font-weight:bold;
  width:300px;
  height:40px;
  border:none;
  text-align:center;
  border-radius:5px;
  font-size:17px;
  margin-left:15px;
}